<template>
  <div class="modal fade" tabindex="-1" role="dialog" id="freightUpdateModal">
    <div class="modal-dialog modal-dialog-centered">
      <div class="modal-content">
        
        <div class="modal-body">
          <freight-plus-form ref="updateFormREF" :isPlus="false" @update_Father="update"></freight-plus-form>
        </div>
        <div class="modal-footer">
          
          <button class="btn btn-main" @click="submitFreightPlus">保存</button>
            
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tool from '@/common/es/tool'
import conn from '@/service/conn'
import conn_util from '@/common/es/conn_util'

import FreightPlusForm from '../Plus/FreightPlusForm'

export default {
  name: 'FreightUpdated',
  components: { FreightPlusForm },
  props: {
    freight: {
      type: Object
    }
  },
  methods: {
    submitFreightPlus() {

      this.$refs.updateFormREF.submitForm()
    },
    update(data, id) {
      
      conn.freight.update(data, id).then(res => {
        if (conn.judge_response(res)) {
          console.log(res)
          tool.showToast('修改成功！！！')
          setTimeout(() => {
            tool.closeAllModal()
            this.$emit('signelRefreash_Father')
          }, 700)
        }
      })
      
    },
    locationUpdateData(data) {
      this.$refs.updateFormREF.locationData(data)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


